import { Link } from "gatsby"
import { useStaticQuery, graphql } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

const Header = ({ siteTitle }) => {
  const data = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "product_images/nu_classics_round.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);
  return (
    <header>
        <div class="wrapper">
          <nav class="header-nav" role="navigation" aria-label="Main">
            <ul>
              <li><Link to="/">Shop</Link></li>
              <li><Link to="/about">About</Link></li>
              <li>
                <form action="https://www.paypal.com/cgi-bin/webscr" method="post" >
                  <input type="hidden" name="cmd" value="_s-xclick"/>
                  <input type="hidden" name="encrypted" value="-----BEGIN PKCS7----------END PKCS7-----"/>
                  <button class="fake-anchor" type="submit" value="checkout" border="0" name="Checkout" alt="PayPal - The safer, easier way to pay online!">Checkout</button>
                  <img class="paypal-pixel" alt="" border="0" src="https://www.paypalobjects.com/en_US/i/scr/pixel.gif" width="1" height="1"/>
                </form>
              </li>
                
            </ul>
          </nav>
          <div class="branding">
            <Link to="/" title="home">
              <img class="store-logo" srcSet={data.placeholderImage?.childImageSharp.fluid.srcSet} src={data.placeholderImage?.childImageSharp.fluid.src} alt="Danni Studio Home"/>
            </Link>
          </div>
        </div>
        <nav class="header-nav mobile-nav" aria-label="Mobile Main" role="navigation">
          <ul>
              <li><Link to="/">Shop</Link></li>
              <li><Link to="/about">About</Link></li>
            <li>
              <form action="https://www.paypal.com/cgi-bin/webscr" method="post" >
                <input type="hidden" name="cmd" value="_s-xclick"/>
                <input type="hidden" name="encrypted" value="-----BEGIN PKCS7----------END PKCS7-----"/>
                <button class="fake-anchor" type="submit" value="checkout" border="0" name="Checkout" alt="PayPal - The safer, easier way to pay online!">Checkout</button>
                <img class="paypal-pixel" alt="" border="0" src="https://www.paypalobjects.com/en_US/i/scr/pixel.gif" width="1" height="1"/>
              </form>
            </li>
          </ul>
        </nav>
      </header>
  )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
